import React from 'react';
import s from './Header.module.scss';
import { CustomButton } from 'components/CustomButton/CustomButton';
import arrowRight from 'images/arrowRight.svg';
import {useLocation, useNavigate} from 'react-router-dom';
import { BurgerButton } from 'components/BurgerButton/BurgerButton';

import { AppRoutes } from 'pagesReact/appRouter/config';

type button = {
  name: string;
  path: string;
}

const navigation: button[] = [
  {
    name: 'Search',
    path: AppRoutes.SEARCH,
  },
  {
    name: 'Mailbox',
    path: AppRoutes.MAILBOX,
  },
  {
    name: 'Phone',
    path: AppRoutes.PHONE,
  },
  {
    name: 'Pricing',
    path: AppRoutes.PRICING_PAGE,
  }
];

const Header = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const navigationButtons = navigation.map(button => {
    return (
      <div
        className={`${s.navButton} ${button.path === location.pathname && s.active}`}
        onClick={() => navigate(button.path)}
        key={button.name}
      >
        { button.name }
      </div>
    );
  });

  return (
    <div className={s.body}>
      <div className={s.container}>
        <div className={s.navigation}>
          <div className={s.companyName}>
            WEBIA
          </div>
          <div className={s.navigationButtons}>
            { navigationButtons }
          </div>
        </div>
        <div className={s.buttonContainer}>
          <CustomButton
            variant='action'
            text={'Log in'}
            image={arrowRight}
            imagePos='end'
            size={'l'}
          />
        </div>
        <div className={s.burgerContainer}>
          <BurgerButton/>
        </div>
      </div>
    </div>
  );
};

export default Header;