import React, { useState, InputHTMLAttributes, memo, ChangeEvent } from 'react';
import s from './CustomInput.module.scss';
import EyeIcon from 'images/EyeIcon.svg';
import EyeIconSlash from 'images/EyeIconSlash.svg';

type HTMLInputProps = Omit<InputHTMLAttributes<HTMLInputElement>, 'value' | 'onChange' | 'readOnly'>

interface CustomInputProps extends HTMLInputProps {
  className?: string;
  value?: string;
  onChange?: (value: string) => void
  type?: 'text' | 'password' | 'number'
  placeholder?: string;
  autofocus?: boolean
  readOnly?: boolean
  disabled?: boolean
  background?: 'white' | 'grey';
}

const CustomInput = memo((props: CustomInputProps) => {
  const {
    className,
    value,
    onChange,
    type = 'text',
    placeholder,
    autofocus,
    readOnly,
    disabled = false,
    background = 'white',
    ...otherProps
  } = props;

  const [isFocused, setIsFocused] = useState<boolean>(false);
  const [showPassword, setShowPassword] = useState<boolean>(false);

  const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) => {
    onChange?.(e.target.value);
  };

  const handleFocus = () => {
    setIsFocused(true);
  };

  const handleBlur = () => {
    setIsFocused(false);
  };

  const toggleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  return (
    <div className={`${s.inputContainer} ${className}`}>
      <input
        className={`${s.input} ${s[background]}`}
        type={showPassword ? 'text' : type}
        value={value}
        onChange={onChangeHandler}
        disabled={disabled}
        onFocus={handleFocus}
        onBlur={handleBlur}
        {...otherProps}
      />
      {type === 'password' && (
        <>
          {showPassword ?
            <EyeIconSlash className={s.showHideIcon} onClick={toggleShowPassword} /> :
            <EyeIcon className={s.showHideIcon} onClick={toggleShowPassword} />
          }
        </>
      )}
      {placeholder && <div className={`${
        s.customPlaceholder
      } ${
        (isFocused || value) && s.customPlaceholderActive
      } ${
        s[background]
      } ${
        isFocused && s.placeHolderTextActive
      }`}
      >
        {placeholder}
      </div>}
    </div>
  );
});

export { CustomInput };
